import kakaGrid from '@mctech/kaka-grid'

import { isString, isFunction } from '../utils/type'
import { isNumberColumn } from '../utils/column'

import types from './column-type'

const { InlineInputEditor } = kakaGrid.columns.action

function isInlineEditorType (columnType) {
  columnType = isString(columnType)
    ? columnType.toUpperCase()
    : columnType

  const allowTypes = ['DEFAULT', 'NUMBER', 'TREE', 'DATE']

  return !columnType ||
    (
      isString(columnType)
        ? allowTypes.indexOf(columnType) !== -1
        : allowTypes.find(key => columnType instanceof types[key])
    )
}

function convertInputType (columnType, type) {
  columnType = isString(columnType)
    ? columnType.toUpperCase()
    : columnType

  return (
    isNumberColumn(type) ||
    (columnType === 'NUMBER') ||
    (columnType instanceof types.NUMBER)
  )
    ? 'number'
    : (type === 'date' ? 'date' : 'text')
}

export function getAction (option) {
  const {
    action,
    gridReadonly = true,
    readonly: optionReadonly = gridReadonly,
    type,
    columnType
  } = option
  const inputOptions = {
    type: convertInputType(columnType, type),
    classList: ['al-right'],
    readonly: rec =>
      !isFunction(optionReadonly)
        ? optionReadonly
        : optionReadonly(rec)
  }
  if (type === 'date') {
    inputOptions.action = function (rec, info) {
      this.open(info.grid, info.cell)
    }
  }
  return !action && isInlineEditorType(columnType)
    ? new InlineInputEditor(inputOptions)
    : undefined
}
