import defineColumn from '../column'

function getColumns (columnOption, option, vm) {
  const { caption, headerStyle } = columnOption
  return columnOption.columns
    ? {
      ...defineColumn(
        { caption, headerStyle, isMultiHeader: true },
        option,
        vm
      ),
      columns: columnOption.columns.map(
        column => getColumns(column, option, vm)
      )
    }
    : defineColumn(columnOption, option, vm)
}

export function kakaOption (option, vm) {
  const {
    theme,
    columns,
    frozenColCount,
    hideHeader,
    singleSelection,
    spanBodyOptions
  } = option

  const { rowHeight = 32, headerRowHeight = 40 } = theme
  const header = columns.map(
    columnOption => getColumns(columnOption, option, vm)
  )

  option.columns = header

  const result = {
    theme,
    header,
    rowHeight,
    frozenColCount,
    headerRowHeight,
    hiddenHeader: hideHeader,
    monitorResize: false,
    singleSelection
  }
  if (spanBodyOptions) {
    result.spanBodyOptions = spanBodyOptions
  }
  return result
}
