import kakaGrid from '@mctech/kaka-grid'
import { isFunction } from '../../utils/type'

const buttonLink = (option, gridOption, vm) => ({
  icon: option.icon,
  caption: option.caption,
  width: option.width || 's',
  style: { color: option.style?.color || gridOption.theme.primaryColor },
  columnType:
    option.columnType ||
    new kakaGrid.columns.type.ButtonColumn({
      linkButton: true,
      caption: option.buttonCaption
    }),
  action: option.action ||
    new kakaGrid.columns.action.ButtonAction({
      disabled: rec => isFunction(option.disabled)
        ? option.disabled(rec)
        : option.disabled,
      action: option.onClick ||
        (
          rec =>
            vm.$emit(
              'buttonclick',
              {
                record: rec,
                field: option.field,
                value: option.field ? rec[option.field] : undefined,
                columnCaption: option.caption,
                buttonCaption: option.buttonCaption
              },
              vm
            )
        )
    })
})

const dangerLink = (option, gridOption, vm) => buttonLink(
  {
    style: { color: gridOption.theme.dangerColor },
    ...option
  },
  gridOption,
  vm
)

const captionLink = (option, gridOption, vm) => buttonLink(
  {
    width: 'l',
    ...option
  },
  gridOption,
  vm
)

