import kakaGrid from '@mctech/kaka-grid'

import { isFunction } from '../../utils/type'

import { addEventHandler } from '../../event/internal-handler'

import { getWidthOption } from '../width'

function changeable (column, rec, gridOption) {
  const { readonly, contentHidden } = column
  const { readonly: gridReadonly } = gridOption
  return !(isFunction(readonly)
    ? readonly(rec)
    : readonly !== undefined
      ? readonly
      : gridReadonly) &&
  !(isFunction(contentHidden) ? contentHidden(rec) : contentHidden)
}

addEventHandler('headerchange', (data, vm) => {
  const { value, field } = data
  const column = vm.gridOption.columns.find(el => el.field === field)

  if (column?.headerType === 'check') {
    vm.records.forEach(rec => {
      if (changeable(column, rec, vm.gridOption)) rec[field] = value
    })
    vm.$emit(value ? 'checkall' : 'uncheckall', data, vm)
    if (field === vm.gridOption.selectedField) {
      vm.$emit(value ? 'allselect' : 'allunselect', vm)
    }
    vm.invalidate()
  }
})

addEventHandler('cellchange', (data, vm) => {
  const { col, value, field, record } = data
  const column = vm.gridOption.columns[col]
  if (column?.type === 'checkBox') {
    if (!value && column?.headerType === 'check') {
      vm.kakaGrid.headerValues.set(field, false)
    }
    if (vm.checkCascade) {
      // å¨vm.recordsä¸­æ¥æ¾å­èç¹,lazeLoadedRecordsä¸­éå½çæ¥æ¾å­èç¹ï¼å°è¿äºå­èç¹çå¼é½è®¾ç½®ævalue
      setChildRecordsChecked(vm, record, value)
      setParentRecordsChecked(vm, record, value, field, column?.headerType)
    }
    vm.invalidate()
    vm.$emit(value ? 'cellcheck' : 'celluncheck', data, vm)
    if (field === vm.gridOption.selectedField) {
      vm.$emit(value ? 'rowselect' : 'rowunselect', record, vm)
    }
  }
})

function setParentRecordsChecked (vm, record, value, field, headerType) {
  if (!value) {
    // å¦ææ¯çº§èæä½ï¼å¹¶ä¸åæ¶éä¸­ï¼é£ä¹ä»çææä¸çº§èç¹å¨é¨åæ¶éæ©
    let parentId = record[vm.parentKeyField]
    while (parentId) {
      const index = vm.kakaGrid.dataSource.getIndexByKey(parentId)
      if (index < 0) {
        break
      } else {
        const parentRecord = vm.kakaGrid.dataSource.get(index)
        parentRecord[vm.gridOption.selectedField] = false
        parentId = parentRecord[vm.parentKeyField]
      }
    }
  } else {
    // å¦æçº§èæä½ï¼å¹¶ä¸æ¯éä¸­ï¼é£ä¹è§å¯å¨é¨å¹³çº§çèç¹ï¼å¦ææ¯å¨é¨é½æ¯éä¸­ç¶æ
    // åå°å¶ç¶äº²è®¾ç½®æéä¸­ï¼å¹¶ä¸éå½æä½
    let parentId = record[vm.parentKeyField]
    while (parentId) {
      const siblings = vm.records.filter(
        v =>
          v[vm.parentKeyField] === parentId && !v[vm.gridOption.selectedField]
      )
      // å¦æå­å¨åå¼èç¹ä¸ºæªéä¸­çç¶æ, ç´æ¥ç»æ­¢
      if (siblings.length > 0) {
        break
      } else {
        // å¦æåå¼èç¹å¨é¨é½éä¸­
        const index = vm.kakaGrid.dataSource.getIndexByKey(parentId)
        if (index < 0) {
          // å¦ææ¾ä¸å°parentï¼è·³åº
          break
        } else {
          const parentRecord = vm.kakaGrid.dataSource.get(index)
          parentRecord[vm.gridOption.selectedField] = true
          parentId = parentRecord[vm.parentKeyField]
        }
      }
    }
    if (headerType === 'check') {
      const notAllSelected = vm.records.some(v => !v[vm.gridOption.selectedField])
      vm.kakaGrid.headerValues.set(field, !notAllSelected)
    }
  }
}

function setChildRecordsChecked (vm, record, value) {
  if (vm.keyField && vm.parentKeyField) {
    const keys = [record[vm.keyField]]
    while (keys.length > 0) {
      const key = keys.shift()
      const records = vm.records
      const children = records.filter(rec => rec[vm.parentKeyField] === key)
      children.forEach(v => {
        v[vm.gridOption.selectedField] = value
        keys.push(v[vm.keyField])
      })
    }
  }
}

const checkBox = (option, gridOption) => {
  const headerType = 'headerType' in option
    ? option.headerType
    : 'check'
  const headerAction = option.headerAction ||
    new kakaGrid.headers.action.CheckHeaderAction({
      disabled: () => {
        return isFunction(option.headerReadonly)
          ? option.headerReadonly()
          : option.headerReadonly
      }
    })

  return {
    headerType,
    headerAction,
    width: getWidthOption(option.width || '40'),
    minWidth: getWidthOption(option.minWidth || '40'),
    maxWidth: getWidthOption(option.maxWidth || '40'),
    columnType: option.columnType || 'check',
    field: option.field || gridOption.selectedField,
    action: new kakaGrid.columns.action.CheckEditor({
      disabled: rec => !changeable(option, rec, gridOption)
    })
    // style (rec) {
    //   return option.hidden?.(rec)
    //     ? {
    //       borderColor: 'rgba(0, 0, 0, 0)',
    //       checkBgColor: 'rgba(0, 0, 0, 0)',
    //       uncheckBgColor: 'rgba(0, 0, 0, 0)'
    //     }
    //     : undefined
    // }
  }
}

export { checkBox }
