import { isFunction } from '../utils/type'

export function checkBoxBorderStyle (rec, option) {
  return (isFunction(option.field)
    ? option.field(rec)
    : rec[option.field || '_selected']
  )
    ? { borderColor: '#008cd6' }
    : { borderColor: '#b9b9b9' }
}
export function headerCheckBoxBorderStyle (option, map) {
  return option.type === 'checkBox' && map.get(option.field || '_selected')
    ? { borderColor: '#008cd6' }
    : { borderColor: '#b9b9b9' }
}

export function readonlyStyle (rec, option, gridOption) {
  return (
    isFunction(option.readonly)
      ? option.readonly(rec)
      : option.readonly !== undefined
        ? option.readonly
        : gridOption.readonly
  )
    ? { uncheckBgColor: '#EAEAEA' }
    : { uncheckBgColor: '#fff' }
}
