<template>
  <div
    v-resize-detector
    style="overflow: hidden"
    @elementresize="onResize" />
</template>

<script>
  import kakaGrid from '@mctech/kaka-grid'

  import { kakaOption } from './kaka-option'
  import { mergeTheme } from './theme'
  import { bind as bindEvents } from '../event'

  const { CachedDataSource, TreeDataSource } = kakaGrid.data

  export default {
    name: 'KakaGrid',
    props: {
      theme: Object,
      records: Array,
      columns: Array,
      keyField: String,
      readonly: {
        type: Boolean,
        default: true
      },
      parentKeyField: String,
      selectedField: String,
      frozenColCount: Number,
      hideHeader: Boolean,
      getRecords: Function,
      hasRecords: Function,
      singleSelection: {
        type: Boolean,
        default: false
      },
      checkCascade: {
        type: Boolean,
        default: true
      },
      spanBodyOptions: Object
    },
    data () {
      return {
        gridOption: null,
        currentRecords: null,
        lazyLoadingRecords: {} // å­æ¾æ­£å¨å¼æ­¥è·årecordsçå¯ä»¥ï¼é²æ­¢å¿«éå¤æ¬¡è¯·æ±ï¼æèè¯·æ±è¿æ¢å¯¼è´çå è½½å¤ä»½æ°æ®çé®é¢
      }
    },
    watch: {
      records: {
        handler (value) {
          if (value && value === this.currentRecords) {
            const { cachedDataSource } = this.gridOption
            const { dataSource } = this.kakaGrid

            if (
              cachedDataSource &&
              cachedDataSource.length !== value.length
            ) {
              cachedDataSource.length = value.length
            } else if (
              !cachedDataSource &&
              dataSource.length !== value.length
            ) {
              dataSource.length = value.length
            }
            this.refreshDatasource()
          } else if (value !== this.currentRecords) {
            // éè¦åæ¶å¤´çcheckBoxçå¨éç¶æ
            this.columns.forEach(v => {
              if (v.type === 'checkBox') {
                this.kakaGrid.headerValues.set(
                  v.field || this.gridOption.selectedField, false
                )
              }
            })
            this.loadRecords(value)
          }
        },
        deep: true
      },
      readonly () {
        this.$nextTick(() => {
          this.kakaGrid.dataSource.refresh()
        })
      }
    },
    mounted () {
      this.initGrid()
    },
    beforeDestroy: function () {
      this.kakaGrid.dispose()
    },
    methods: {
      initGrid () {
        if (this.kakaGrid) this.kakaGrid.dispose()
        const o = this.$options

        const columns = this.getValue('columns')
        const records = this.currentRecords = this.getValue('records')
        const keyField = this.getValue('keyField')
        const parentKeyField = this.getValue('parentKeyField')
        const selectedField = this.getValue('selectedField') || '_selected'
        const frozenColCount = this.getValue('frozenColCount')
        const hideHeader = this.getValue('hideHeader')
        const getRecords = this.getValue('getRecords')
        const hasRecords = this.getValue('hasRecords')
        const singleSelection = this.getValue('singleSelection')
        const spanBodyOptions = this.getValue('spanBodyOptions')
        const readonly = this.readonly
        const theme = mergeTheme(o.theme, this.theme)

        this.gridOption = {
          singleSelection,
          theme,
          columns,
          keyField,
          parentKeyField,
          selectedField,
          frozenColCount,
          getRecords,
          hasRecords,
          hideHeader,
          readonly,
          spanBodyOptions
        }
        this.$nextTick(() => {
          const grid = new kakaGrid.ListGrid({
            parentElement: this.$el,
            ...kakaOption(this.gridOption, this)
          })
          bindEvents(this, grid)
          this.kakaGrid = grid

          if (records) this.loadRecords(records)
        })
      },
      getValue (key) {
        const o = this.$options
        return this[key] || o[key]
      },
      async loadRecords (records) {
        this.lazyLoadingRecords = {}
        this.currentRecords = records

        const o = this.gridOption
        const { keyField, parentKeyField, getRecords, hasRecords } = o
        const options = {
          keyField,
          parentKeyField
        }
        if (getRecords) {
          options.hasChildren = hasRecords || (rec => {
            return rec ? !rec.isLeaf : true
          })
          options.getChildren = async (rec, all) => {
            const parentKey = rec ? rec[keyField] : null
            if (!rec || this.lazyLoadingRecords[parentKey]) return []
            this.lazyLoadingRecords[parentKey] = true
            const data = await getRecords(rec)
            if (this.checkCascade) {
              (data.records || data).forEach(v => {
                v[this.gridOption.selectedField] =
                  rec[this.gridOption.selectedField] ||
                  this.kakaGrid.headerValues.get(this.gridOption.selectedField)
              })
            }
            records.push(...(data.records || data))
            delete this.lazyLoadingRecords[parentKey]
            // console.log(data)
            return data
          }
        }
        if (keyField && parentKeyField) {
          o.cachedDataSource = CachedDataSource.ofArray(records)
          this.kakaGrid.dataSource = new TreeDataSource(
            o.cachedDataSource,
            options
          )
        } else {
          this.kakaGrid.records = records
        }
        this.$emit('load', this)
      },
      expandToLevel (level) {
        this.kakaGrid.dataSource.expandToLevel(level)
      },
      expandToKey (key) {
        this.kakaGrid.dataSource.expandToKey(key)
      },
      refreshDatasource () {
        this.kakaGrid.dataSource.refresh()
      },
      invalidate () {
        this.kakaGrid.invalidate()
      },
      onResize () {
        if (!this.kakaGrid) return
        if (this.resizeTimer) clearTimeout(this.resizeTimer)
        this.resizeTimer = setTimeout(() => {
          this.kakaGrid.resize()
          this.resizeTimer = null
        }, 200)
      },
      selectAll () {
        this.records.forEach(v => {
          v[this.gridOption.selectedField] = true
        })
        this.kakaGrid.headerValues.set(this.gridOption.selectedField, true)
        this.invalidate()
      },
      unSelectAll () {
        this.records.forEach(v => {
          v[this.gridOption.selectedField] = false
        })
        this.kakaGrid.headerValues.set(this.gridOption.selectedField, false)
        this.invalidate()
      },
      getSelectedRecords () {
        return this.records.filter(
          rec => rec[this.gridOption.selectedField]
        )
      }
    }
  }
</script>

<style lang="postcss">
  .kaka-grid__inline-lookup__dropdown-item {
    height: 40px;
    line-height: 40px;
    border-bottom: 1px solid #f2f2f2;
    background: #fff;
  }
  .kaka-grid__inline-lookup__dropdown-item--first {
    border-top: 1px solid #f2f2f2;
  }
  .kaka-grid__inline-lookup__dropdown-item:hover {
    color: #008cd6;
  }
  .kaka-grid__inline-lookup__dropdown-item[data-select] {
    color: #008cd6
  }
  .kaka-grid__inline-input,
  .kaka-grid__inline-lookup__input,
  .kaka-grid__inline-textarea {
    outline: none;
    border: 1px #008cd6 solid !important;
    border-radius: 2px;
  }

</style>
