import kakaGrid from '@mctech/kaka-grid'
import { getCellStyle } from '../../utils/cell-style'
import { getReadonly } from '../../utils/readonly'

const treeLink = (option, gridOption, vm) => {
  return {
    columnType: option.columnType || new kakaGrid.columns.type.TreeColumn({
      cellStyle: rec => getCellStyle(rec, option, gridOption)
    }),
    style: rec => getReadonly(rec, option, gridOption)
      ? {}
      : { color: option.style?.color || gridOption.theme.primaryColor },
    action: option.action ||
      new kakaGrid.columns.action.ButtonAction({
        disabled: rec => getReadonly(rec, option, gridOption),
        action: option.onClick ||
          (
            rec =>
              vm.$emit(
                'buttonclick',
                {
                  record: rec,
                  field: option.field,
                  value: option.field ? rec[option.field] : undefined,
                  columnCaption: option.caption,
                  buttonCaption: option.buttonCaption
                },
                vm
              )
          )
      })
  }
}

const tree = (option, gridOption, vm) => {
  return {
    columnType: option.columnType || new kakaGrid.columns.type.TreeColumn({
      cellStyle: rec => getCellStyle(rec, option, gridOption)
    })
  }
}

export {
  tree,
  treeLink
}
