import kakaGrid from '@mctech/kaka-grid'
import dayjs from 'dayjs'
import round from 'lodash.round'
import { getCellStyle } from './cell-style'

export function isNumberColumn (type) {
  return ['percent', 'unitPrice', 'quantity', 'price', 'number'].includes(type)
}

export function convertColumnType (
  type,
  columnOption,
  format,
  { option, gridOption }
) {
  const { Column } = kakaGrid.columns.type

  if (type === 'date') {
    const dateFormat = format || 'YYYY-MM-DD'

    columnOption.columnType = new Column({
      convert: value => value ? dayjs(value).format(dateFormat) : '',
      cellStyle: rec => getCellStyle(rec, option, gridOption)

    })
  } else if (isNumberColumn(type)) {
    const digits = isNaN(format)
      ? ['price', 'percent'].indexOf(type) === -1
        ? 6
        : 2
      : parseInt(format)

    columnOption.columnType = new Column({
      convert: value => !value && value !== '0' && value !== 0 ? '' : round(value, digits),
      cellStyle: rec => getCellStyle(rec, option, gridOption)
    })
  } else if (type === 'no') {
    columnOption.columnType = new Column({
      convert: (value, displayValue, cell, frozenInfo) => {
        return cell.row - frozenInfo.frozenRowCount + 1
      },
      cellStyle: rec => getCellStyle(rec, option, gridOption)
    })
  } else {
    columnOption.columnType = new Column({
      cellStyle: rec => getCellStyle(rec, option, gridOption)
    })
  }
}
