import { getReadonly } from './readonly'

export function getCellStyle (rec, option, gridOption) {
  const readonly = getReadonly(rec, option, gridOption)
  const result = !readonly &&
  ['captionLink', 'dangerLink', 'removeLink', 'buttonLink', 'modifyLink', 'treeLink'].indexOf(option.type) === -1
  return result
    ? { innerBox: 'dashed' }
    : {}
}
