import { isFunction } from '../utils/type'
import { convertColumnType } from '../utils/column'
import { getAppearance } from '../utils/icons'

import { getAction } from './action'
import { presets } from './preset'
import { getWidthOption } from './width'

import { checkBoxBorderStyle, headerCheckBoxBorderStyle, readonlyStyle } from './style'

const UNEXPECTED_PROPERTIES = ['preset', 'disabled', 'buttonCaption', 'onClick']

export default function defineColumn (option, gridOption, vm) {
  const {
    type,
    align,
    color,
    style,
    minWidth,
    maxWidth,
    format,
    headerStyle
  } = option

  const { theme } = gridOption

  const presetOption = presets[type]?.(option, gridOption, vm)
  const width = option.width || presetOption?.width || 'm'
  const rightTypes = ['price', 'percent', 'unitPrice', 'quantity', 'number']
  const centerTypes = ['date', 'no', 'checkBox', 'modifyLink', 'removeLink']
  const textAlign =
    align ||
    presetOption?.align ||
    (rightTypes.includes(type)
      ? 'right'
      : centerTypes.includes(type)
        ? 'center'
        : option.isMultiHeader ? 'center' : 'left')

  const columnOption = {
    action: getAction({
      ...option,
      gridReadonly: gridOption.readonly,
      presetOption
    }),
    ...option,
    ...presetOption,
    ...getWidthOption(width, minWidth, maxWidth),
    style: rec => ({
      textAlign,
      padding: [0, 8],
      textOverflow: 'ellipsis',
      checkBgColor: '#008cd6',
      inputPadding: [0, 7, 1, 7],
      ...(checkBoxBorderStyle(rec, option)),
      ...readonlyStyle(rec, option, gridOption),
      ...(isFunction(presetOption?.style)
        ? presetOption?.style(rec)
        : presetOption?.style),
      ...(color ? { color } : undefined),
      ...((isFunction(option.disabled) ? option.disabled(rec) : option.disabled)
        ? { color: theme.disabledColor, ...getAppearance(type, false) }
        : getAppearance(type, true)),
      ...(isFunction(style) ? style(rec) : style)
    }),
    headerStyle: col => ({
      textAlign,
      padding: [0, 8],
      textOverflow: 'ellipsis',
      checkBgColor: '#008cd6',
      ...(headerCheckBoxBorderStyle(option, col)),
      ...(isFunction(headerStyle) ? headerStyle(col) : headerStyle)
    })
  }
  if (!columnOption.columnType) {
    convertColumnType(type, columnOption, format, {
      option,
      gridOption
    })
  }

  UNEXPECTED_PROPERTIES.forEach(key => {
    delete columnOption[key]
  })
  return columnOption
}
