import eventTypes from './types'

import { applyInternalHandler } from './internal-handler'

export function bind (vm, grid) {
  Object.keys(eventTypes).forEach(raw => {
    grid.listen(raw, function () {
      const eventType = eventTypes[raw]
      applyInternalHandler(eventType, ...arguments, vm)

      const context = {}
      vm.$emit(eventType, ...arguments, vm, context)
      if ('result' in context) return context.result
    })
  })
}
