import kakaGrid from '@mctech/kaka-grid'

import { isFunction } from '../../utils/type'
import { getCellStyle } from '../../utils/cell-style'

function getOptions (comboOptions, valueField, captionField) {
  return async rec => {
    const options = isFunction(comboOptions)
      ? await comboOptions(rec)
      : comboOptions
    const records = (options || []).map(el => (
      valueField in Object(el)
        ? el
        : { [valueField]: el, [captionField]: el }
    ))
    return records
  }
}

const comboBox = (option, gridOption, vm) => {
  const valueField = option.lookupValueField || 'value'
  const captionField = option.lookupLabelField || 'label'

  const records = getOptions(
    option.options,
    valueField,
    captionField
  )

  const readonly = option.readonly === true && gridOption.readonly === true

  return {
    columnType: option.columnType ||
      new kakaGrid.columns.type.LookupColumn({
        records,
        valueField,
        captionField,
        cellStyle: rec => getCellStyle(rec, option, gridOption)
      }),
    action: readonly !== true
      ? option.action || new kakaGrid.columns.action.InlineLookupEditor({
        records,
        valueField,
        captionField,
        readonly: rec => {
          return isFunction(option.readonly)
            ? option.readonly(rec)
            : option.readonly !== undefined
              ? option.readonly
              : gridOption.readonly
        },
        filterFields: option.filterFields || [option.captionField, option.valueField],
        allowOtherInput: option.AcceptInputValue,
        disableFilterSort: option.sortByInput !== true,
        disableFilterRecords: option.FilterByInput !== true,
        valueType: option.type === 'number' ? 'number' : 'string'
      })
      : undefined
  }
}

export {
  comboBox
}
