import warn from '../../utils/warn'

import { tree, treeLink } from './tree'
import { comboBox } from './combo-box'
import { checkBox } from './check-box'
import {
  buttonLink,
  dangerLink,
  captionLink,
  modifyLink,
  removeLink
} from './button'

function setColumnPreset (data) {
  Object.keys(data).forEach(key => {
    if (presets[key]) warn(`Column preset "${key}" has been overwritten.`)
    presets[key] = data[key]
  })
  Object.assign(presets, data)
}

const presets = {
  tree,
  comboBox,
  checkBox,
  buttonLink,
  dangerLink,
  captionLink,
  modifyLink,
  removeLink,
  treeLink
}

export {
  presets,
  setColumnPreset
}
