export function getWidthOption (width, minWidth, maxWidth) {
  const option = { width }

  switch (width) {
    case 'xs':
      option.width = 60
      break
    case 's':
      option.width = 80
      break
    case 'm':
      option.width = 135
      break
    case 'l':
      option.width = 200
      break
    case 'xl':
      option.width = 300
      break
    // case 'auto':
    //   option.minWidth = minWidth || 100
    //   option.maxWidth = maxWidth || 600
    //   break
  }
  return option
}
